
import { ref, defineComponent, onMounted } from 'vue';
import VendorCard from './VendorCard.vue';
import VendorModel from './VendorModel';
import NewVendorModal from './NewVendorModal.vue';
import VendorService from './VendorService';

export default defineComponent({
  components: {
    VendorCard,
    NewVendorModal,
  },
  setup() {
    const vendorModal = ref(false);
    const vendorRef = ref();
    const vendors = ref<VendorModel[]>([]);
    const isEditing = ref(false);

    const getVendors = async () => {
      try {
        vendors.value = await VendorService.getVendors();
      } catch (error) {
        console.log(error);
      }
    };

    // const vendors = [
    //   {
    //     id: '50-',
    //     name: 'Hello',
    //     phone: '03321021700',
    //     bankName: 'HBL',
    //     accountNo: '23997980857903',
    //     balance: 500,
    //     address: 'DHA',
    //     createdAt: '12-01-2020',
    //   },
    //   {
    //     id: '50-',
    //     name: 'Hello',
    //     phone: '03321021700',
    //     bankName: 'HBL',
    //     accountNo: '23997980857903',
    //     balance: 500,
    //     address: 'DHA',
    //     createdAt: '12-01-2020',
    //   },
    // ];

    const newVendorAction = () => {
      isEditing.value = false;
      vendorRef.value.resetForm();
      vendorModal.value = true;
    };

    const deleteVendor = async (id: string) => {
      console.log(`Delete Brand ${id}`);
    };

    const editVendor = async (vendor: VendorModel) => {
      isEditing.value = true;
      vendorRef.value.setToEdit(vendor);
      vendorModal.value = true;
    };

    const refreshList = async () => {
      console.log('reset-list');
      getVendors();
      vendorModal.value = !vendorModal.value;
    };

    onMounted(async () => {
      getVendors();
    });

    return {
      vendors,
      vendorRef,
      editVendor,
      isEditing,
      vendorModal,
      deleteVendor,
      refreshList,
      newVendorAction,
    };
  },
});
